import React, { useState } from 'react'

import { Button, TextField } from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import InputAdornment from '@mui/material/InputAdornment';

import '../../stylesheets/Search.css';

//  **  **
//  filter  :  string    
// ***   ->        

//  ->   ->  feed  ->   -> event: isfollowing: true    

//    
// -user id -> filter ->  user nickname 

// < style  >
//  search  input  
//   default   

const Data = [
  { "strUserName": "", "todo_id": "0", "title": "" },
  { "strUserName": "", "todo_id": "1", "title": "" },
  { "strUserName": "", "todo_id": "5", "title": "" },
  { "strUserName": "", "todo_id": "7", "title": "8 " }
]
//console.log(Data);

export default function Search() {

    const [searchTerm, setSearchTerm] = useState("");
  
    return (
      <div className="search-search-list-wrap" >
            {/* textfield underline   */}
            <TextField 
                className="search-blank"
                fullWidth  
                hiddenLabel
                id="filled-basic"
                size="small"
                variant="filled"
                placeholder="email ,ID "
                sx={{ boxShadow: 'none',
                '& .MuiFilledInput-root': {
                  backgroundColor:'#f5f5f5',
                  borderRadius: 2,
                  fontSize: 14,
                  color: '#080808',}
                }}    
                onChange={(event) => {
                  setSearchTerm(event.target.value);
                }}
        
                InputProps={{
                  disableUnderline: true,
                startAdornment: (
                  <InputAdornment position="start">
                    <SearchIcon fontSize="small" className="search-list-icon" />
                  </InputAdornment>
                ),
              }}
            >
            </TextField>

            {Data.filter((data)=> {
              if (searchTerm === "") {
                return data
              } else if (data.strUserName.toLowerCase().includes(searchTerm.toLowerCase())) {
                return data
              } else if (data.title.toLowerCase().includes(searchTerm.toLowerCase())) {
                return data
              }
            }).map(data=>{
              return(
                <React.Fragment key={data.todo_id}>
                  <div className="search-list-box" key={data.todo_id}>
                    <Button className="search-list-button" id={data.todo_id} name={data.todo_id}>
                        <div className="search-list-name" id={data.strUserName} name={data.strUserName}>
                          <p>{data.strUserName}</p>
                        </div>
                        <div className="search-list-text" id={data.title} name={data.title}>
                          <p>{data.title}</p>
                        </div>
                    </Button>
                  </div>

                </React.Fragment>
               
               );

              })}
                
    
      </div>
    
    )}