import React from 'react';
import { useHistory, useParams } from 'react-router-dom';

import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import IconButton from '@mui/material/IconButton';
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import AddIcon from '@material-ui/icons/Add';

import '../../stylesheets/Nav.css';


export default function BasicNavBar () {

  //useHistory  
  let history = useHistory();


  /*  onClick  */

  //   
  function moveGoalForm(){
    window.location.replace("/goals/goalForm")
  }

  //url pathname 
  let urlPath = window.location.pathname;
  let { id } = useParams();

  return (
    <Box className="basic_nav_box">
      <AppBar className="basic_nav_appbar" >
        <Toolbar>
        <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="back"
            sx={{ mr: 2 }}
            onClick={history.goBack} 
          >
         <NavigateBeforeIcon />
          </IconButton>
          <Typography className="navbar_title" variant="h6" component="div">

          {/*  navbar   */}
          {
             urlPath.includes(`signin`)? `` : urlPath.includes(`join`)?  `` 
             : urlPath.includes(`goals`)? `` : urlPath.includes(`exploreSearch`)? `` 
             : urlPath.includes(`termUse`)? `` : urlPath.includes(`policy`)? ` ` 
             : urlPath.includes(`setting`)? `` : urlPath.includes(`text`)? `` 
             : urlPath.includes(`announcements`)? `` : urlPath.includes(`routines`) ? `    ` : 
             urlPath.includes(`orderitem`) ? ` ` : null
           }
          </Typography>
          
          <IconButton
            size="large"
            edge="start"
            color="inherit"
            aria-label="back"
            sx={{ mr: 2 }}
          >
           {
             urlPath ===`/goals`? <AddIcon onClick={moveGoalForm} /> : null
           }
        </IconButton>
        </Toolbar>
      </AppBar>
    </Box>
  );
}