import React ,  { useState } from 'react';
import { useForm } from "react-hook-form";
import { Button, TextField } from '@mui/material';
import { Link } from 'react-router-dom';

import { postUserLogin } from '../../api/apiCommunicate';
import '../../stylesheets/Signin.css';


function Signin(){

    const { register, handleSubmit, formState: { errors }, watch } = useForm();
    const [result, setResult] = useState("");
    console.log("watch",watch())

    const onSubmit = (data) => {
      postUserLogin(data)
    };

    const onError = (error) => {
      console.log(error)
    };

    return (

    <form onSubmit={
      handleSubmit(onSubmit)
      }>
    <TextField
    {...register("account", {required: true})} 
        id="standard-user-id-input"
        className='user-control-input'
        label=" "
        type="text"
        autoComplete="current-user-id"
        variant="standard"
        helperText="Please enter Account"
        margin="dense"
        fullWidth 
      />
      {errors.account?.type === "required" &&  <span className='error_message'> .</span>}
      <TextField
       {...register("password", {required: true})} 
        id="standard-password-input"
        label=""
        type="password"
        autoComplete="current-password"
        variant="standard"
        helperText="Please enter Password"
        margin="dense"
        fullWidth 
      />
      {errors.password?.type === "required" &&  <span className='error_message'> .</span>}
      <Button type="submit" id="submit_btn"></Button>

      <Link to="/"><p className='forgotPwd'> ?</p></Link>
      </form>
      )

}

export default Signin;

