/*  API   */

//GET   
//dateYm=yyyy-mm
export const getTodosOverviewData = async (numUserId,localDate) => {

  //axios
  await axios({
    method: `GET`,
    url: `${todoHost}/todos/overview`,
    params: {
      userId: numUserId,
      dateYm: localDate
    },
    header: {
      headers
    }
  })
  .then(Response => Response.data)
  .then(Response => !Response.success ? alert("  .") : null)
  .catch((Error)=>{console.log(Error)})
  
    //  res.data success true   null  todoData objFeedCalendarOverview   
  return Response.data;
} 

