export const postUserJoin = async (data) => {

  //axios
  await axios({
    method: `POST`,
    url: `${userHost}/join`,
    params: {
      data
    },
    headers:{
      headers
    }
  })
  .then(res => res.data)
  .then(res => !res.success ? alert("  ") : 
   window.location.reload(`/signin`))
} 



//

export const postUserLogin = async (data) => {

  //axios
  await axios({
    method: `POST`,
    url: `${userHost}/login`,
    params: {
      data
    },
    headers:{
      headers
    }
  })
  .then(res => res.data)
  .then(res => !res.success ? alert("  ") : 
   window.location.reload(`/main`))
} 


