import React , { useState } from 'react';
import { appendErrors, useForm } from "react-hook-form";
import { Link } from 'react-router-dom';
import { Button, TextField } from '@mui/material';

import { postUserJoin } from '../../api/apiCommunicate'
import '../../stylesheets/Signin.css';

export default function Join(){

    const { register, handleSubmit, formState: { errors }, watch } = useForm({ mode: "onChange" });
    const [result, setResult] = useState("");
    console.log("watch",watch())

    const onSubmit = (data) => {
      setResult(JSON.stringify(data));
      console.log(result, errors)
      postUserJoin(JSON.stringify(data))
    }
    const onError = (error) => {
      console.log(error)
    };

    return (

    <form onSubmit={handleSubmit(onSubmit,onError)}>
      <TextField
      {...register("userId", {required: true, message: " ."})} //   
        id="standard-user-id-input"
        label=""
        type="text"
        autoComplete="current-id"
        variant="standard"
        helperText="Please enter Account"
        margin="dense"
        fullWidth 
      />
      {errors.userId?.type === "required" &&  <span className='error_message'> .</span>}
      <TextField
      {...register("email", {required: true, message: "  ."})} //   
        id="standard-email-input"
        label=""
        type="email"
        autoComplete="current-email"
        variant="standard"
        helperText="Please enter Email"
        margin="dense"
        fullWidth 
      />
      {errors.email?.type === "required" &&  <span className='error_message'>  .</span>}
      <TextField
       {...register("password", {required: true, minLength:{ value: 3, message: " 4  ."}})}  //   
        id="standard-password-input"
        label=""
        type="password"
        autoComplete="current-password"
        variant="standard"
        helperText="Please enter Password"
        margin="dense"
        fullWidth 
      />
      {errors.password?.type === "required" &&  <span className='error_message'> 4  .</span>}
      {errors.password?.type === "maxLength" && errors.password.message}
      <TextField
       {...register("name", {required: true, minLength:{ value: 2, message: " "}})}  //   
        id="standard-name-input"
        label=""
        type="name"
        autoComplete="current-name"
        variant="standard"
        helperText="Please enter Name"
        margin="dense"
        fullWidth 
      />
      {errors.name?.type === "required" &&  <span className='error_message'> </span>}
      <TextField
       {...register("introText", {required: true, message: " ."})}  //   
        id="standard-intro-input"
        label=""
        type="text"
        autoComplete="current-intro"
        variant="standard"
        helperText="Please enter Intro"
        margin="dense"
        fullWidth 
      />
       {errors.introText?.type === "required" &&  <span className='error_message'> .</span>}
      <Button type="submit" id="submit_btn"></Button>
      
      <div className='join_txt'>
          <p>
          : <br />
              .<br />
                .
          </p>
          <p>      . &nbsp;
          <Link to="/text/use" className='termOfUse'></Link> <Link className='policy' to="/text/policy"> </Link>
          </p>
      </div>
      </form>
)

}

